
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck
import AppModal from "@/utils/AppModal.vue";
import AppButton from "@/utils/AppButton.vue";
import Vue from "vue";
import { mapMutations } from "vuex";

import { formatDate } from "@/utils/formatDate";

export default Vue.extend({
  components: { AppModal, AppButton },
  data() {
    return {
      nama: "",
      isAdmin: false,
      dialog: false,
      headers: [
        { text: "Username", value: "username" },
        { text: "Nama", value: "nama" },
        { text: "Type User", value: "isAdmin" },
        { text: "Dibuat Pada", value: "createdAt" },
        { text: "Aksi", value: "action" },
      ],
      items: [],
      addDialog: false,
      passwordDialog: false,
      deleteDialog: false,
      newPassword: "",
      account: "",
    };
  },
  methods: {
    ...mapMutations(["setShowModal", "unsetShowModal"]),
    ...mapMutations("snackbar", ["showSnackbar"]),
    formatDate,
    showDialog() {
      this.dialog = true;
      this.setShowModal();
    },
    hideDialog() {
      this.dialog = false;
      this.unsetShowModal();
    },
    async addUser() {
      try {
        await this.axios.post("/user", {
          nama: this.nama,
          isAdmin: this.isAdmin,
        });

        this.hideDialog();
        this.addDialog = false;

        this.showSnackbar({
          msg: "Berhasil Menambahkan Akun",
          color: "success",
        });

        const result = await this.axios.get("/user/all");
        this.items = result.data;
      } catch (error) {
        this.showSnackbar({
          msg: error,
          color: "error",
        });
        console.log(error);
      }
    },
    async resetPassword(username) {
      try {
        const result = await this.axios.put("/user/password", { username });

        this.showSnackbar({
          msg: "Reset Password Berhasil",
          color: "success",
        });

        this.passwordDialog = true;
        this.newPassword = result.data.newPassword;
        this.showDialog();

        console.log(result);
      } catch (error) {
        this.showSnackbar({
          msg: error,
          color: "error",
        });
        console.log(error);
      }
    },
    async deleteUser() {
      try {
        await this.axios.delete(`/user/${this.account}`);

        this.showSnackbar({
          msg: "Akun berhasil dihapus",
          color: "success",
        });

        this.deleteDialog = false;
        this.account = "";
        this.hideDialog();

        this.getUsers();
      } catch (error) {
        this.showSnackbar({
          msg: error,
          color: "error",
        });
        console.log(error);
      }
    },
    async revokeAdmin(username) {
      await this.axios.patch(`/user/${username}/admin`, { isAdmin: false });
      this.showSnackbar({
        msg: "Revoke Admin Berhasil",
        color: "success",
      });

      this.getUsers();
    },
    async grantAdmin(username) {
      await this.axios.patch(`/user/${username}/admin`, { isAdmin: true });
      this.showSnackbar({
        msg: "Grant Admin Berhasil",
        color: "success",
      });
      this.getUsers();
    },
    async getUsers() {
      const result = await this.axios.get("/user/all");
      this.items = result.data;
    },
  },
  async created() {
    this.getUsers();
  },
});
